<template>
  <div class="page-header-index-wide">
    <a-card v-if="isTopView" :bordered="false" :bodyStyle="{ padding: '16px 0', height: '100%' }" :style="{ height: '100%' }">
      <div class="device-info-main">
        <div class="device-info-left" v-show="isSuperAdminUser">
          <a-input-search
            style="margin-bottom: 8px"
            placeholder="输入合作方名称"
            enter-button
            @search="onChange"
          />
          <a-list
            size="small"
            bordered
            :data-source="thirdPartyItems"
            class="ant-menu-inline"
            :loading="leftLoading"
          >
            <a-list-item
              class="ant-menu-item"
              :class="item.id === queryParams.parterId ? 'ant-menu-item-selected' : ''"
              slot="renderItem"
              slot-scope="item, index"
              rowKey="id"
              :data-id="item.id"
              :key="index"
              @click="onTpSearch(item.id)"
            >
              {{ item.partner_name }}
            </a-list-item>
            <div slot="header" @click="onSearchAll()">全部</div>
          </a-list>
        </div>
        <div class="device-info-right" :class="!isSuperAdminUser ? 'padding0' : ''">
          <page-content>
            <div class="table-page-search-wrapper">
              <a-form layout="inline">
                <a-row :gutter="24">
                  <a-col :md="6" :sm="12">
                    <a-form-item label="关键字">
                      <a-input
                        v-model="queryParams.keywords"
                        placeholder="输入设备名称等"
                        style="width: 220px"
                      />
                    </a-form-item>
                  </a-col>
                  <a-col :md="6" :sm="12">
                    <a-form-item label="设备状态">
                      <a-select
                        v-model="queryParams.status"
                        placeholder="全部状态"
                        default-value=""
                        style="width: 200px"
                      >
                        <a-select-option value="">全部状态</a-select-option>
                        <a-select-option
                          v-for="(value, key) in statusItems"
                          :key="key"
                          :value="key"
                        >{{ value }}</a-select-option
                        >
                      </a-select>
                    </a-form-item>
                  </a-col>
                  <a-col :md="6" :sm="12">
                    <a-button type="primary" @click="refresh(false)">查询</a-button>
                    <a-button style="margin-left: 8px" @click="resetSearchForm()">重置</a-button>
                  </a-col>
                </a-row>
              </a-form>
            </div>
            <a-table
              :columns="columns"
              :data-source="nvrDevices"
              :pagination="false"
              :rowSelection="rowSelection"
              ref="table"
              :loading="loading"
              rowKey="id"
            >
              <template slot="device_name" slot-scope="text, record">
                <edit-table-cell :text="text" @change="onCellChange(record.id, 'device_name', $event)" />
              </template>            
              <span slot="time" slot-scope="time">{{ time | filterTime(time) }}</span>
              <template slot="status" slot-scope="status, record">
                <a-badge :status="status | statusTypeFilter" :text="record.status_title" />
              </template>
              <template slot="operation" slot-scope="text, record">
                <space>
                  <!-- :routeLink="`/device-nvr/${record.id}/edit`" -->
                  <!-- <permission-button
                    code="device-nvr-edit"
                    :isCheck="false"
                    title="编辑"
                    v-if="hasEdit"
                    type="modal"
                    :useBtnTag="false"
                    @handle-click="handleEdit(record.id)"
                  /> -->
                  <permission-button
                    code="device-nvr-channel"
                    :isCheck="false"
                    :routeLink="`/device-nvr/${record.id}/channels`"
                    title="查看通道"
                    type="tab"
                  />
                  <permission-button
                    code="device-nvr-show"
                    :isCheck="false"
                    :routeLink="`/device-nvr/${record.id}/show`"
                    title="详情"
                    type="tab"
                  />
                </space>
              </template>
            </a-table>
            <lz-pagination
              :page="paginator"
              @change="changePaginator"
              @showSizeChange="showSizeChange"
              :autoPush="false"
            />
            <device-nvr-form
              ref="nvrFormModal"
              :visible="visible"
              :loading="confirmLoading"
              @cancel="handleCancel"
              @ok="handleOk"
            />
          </page-content>
        </div>
      </div>
    </a-card>
    <router-view v-else/>
  </div>
</template>

<script>
import { baseMixin } from '@/store/app-mixin'
import { getThirdPartyItems } from '@/api/third-party'
import { editNvr, getNvrDevices, getStatusItems } from '@/api/device'
import { message } from 'ant-design-vue'
import Space from '@c/Space'
import LzPagination from '@c/LzPagination'
import PageContent from '@c/PageContent'
import LzPopconfirm from '@c/LzPopconfirm'
import { getRouteByCode } from '@/libs/utils'
import moment from 'moment'
import PermissionButton from '@/components/PermissionButton.vue'
const deviceNvrForm = () => import('./Form.vue')
import { EditTableCell } from '@c/'
export default {
    data () {
        return {
            statusItems: [],
            thirdPartyItems: [],
            nvrDevices: [],
            paginator: null,
            hasEdit: false,
            hasDelete: false,
            leftLoading: false,
            leftQueryParams: {
                keywords: ''
            },
            visible: false,
            confirmLoading: false,
            loading: false,
            queryParams: {
                keywords: '',
                status: '',
                parterId: 0
            },
            selectedRowKeys: [],
            selectedRows: [],
            columns: [
                {
                    title: '名称',
                    dataIndex: 'device_name',
                    key: 'device_name',
                    width: 200,
                    scopedSlots: { customRender: 'device_name' }
                },
                // {
                //     title: '设备SN',
                //     dataIndex: 'device_sn',
                //     key: 'device_sn'
                // },
                {
                    title: '国标编码ID',
                    dataIndex: 'device_id',
                    key: 'device_id',
                    width: 120
                },
                {
                    title: '通道数',
                    dataIndex: 'channel_num',
                    key: 'channel_num'
                },
                {
                    title: '设备状态',
                    dataIndex: 'status',
                    key: 'status',
                    scopedSlots: { customRender: 'status' }
                },
                {
                    title: '制造商',
                    dataIndex: 'manufacturer',
                    key: 'manufacturer'
                },
                {
                    title: '模型',
                    dataIndex: 'device_model',
                    key: 'device_model'
                },
                // {
                //     title: '固件版本',
                //     dataIndex: 'firmware',
                //     key: 'firmware'
                // },
                {
                    title: 'ip',
                    dataIndex: 'local_ip',
                    key: 'local_ip'
                },
                {
                    title: '服务端口',
                    dataIndex: 'local_sip_port',
                    key: 'local_sip_port'
                },
                {
                    title: '所属合作方',
                    dataIndex: 'partner_title',
                    key: 'partner_title'
                },
                // {
                //     title: '设备所在地',
                //     dataIndex: 'address',
                //     key: 'address'
                // },
                {
                    title: '最新上线',
                    dataIndex: 'lastOnlineTime',
                    key: 'lastOnlineTime',
                    scopedSlots: { customRender: 'time' }
                },
                {
                    title: '上次离线',
                    dataIndex: 'lastOfflineTime',
                    key: 'lastOfflineTime',
                    scopedSlots: { customRender: 'time' }
                },
                {
                    title: '创建时间',
                    dataIndex: 'createdTime',
                    key: 'createdTime',
                    scopedSlots: { customRender: 'time' }
                },
                {
                    title: '操作',
                    dataIndex: 'operation',
                    scopedSlots: { customRender: 'operation' }
                }
            ]
        }
    },
    mixins: [baseMixin],
    components: {
        LzPopconfirm,
        PageContent,
        LzPagination,
        Space,
        PermissionButton,
        deviceNvrForm,
        EditTableCell
    },
    mounted () {
        if (this.isTopView) {
            if (this.isSuperAdminUser) {
                this.getThirdPartyItems()
            }
            this.getStatusItems()
            this.loadData()
            this.checkPermissions()
        }
    },
    computed: {
        isTopView () {
          return this.$route.path === '/device/nvr/index'
        },
        isSuperAdminUser () {
            return this.isSuperAdmin()
        },
        rowSelection () {
            return {
                selectedRowKeys: this.selectedRowKeys,
                onChange: this.onSelectChange
            }
        }
    },
    methods: {
        getPopupContainer (trigger) {
            return trigger.parentElement
        },
        checkPermissions () {
            const vueRouters = this.$store.state.vueRouters.vueRouters
            let route = getRouteByCode(vueRouters, 'device-nvr-edit')
            if (route) {
                this.hasEdit = true
            }
            route = getRouteByCode(vueRouters, 'device-nvr-lock')
            if (route) {
                this.hasDelete = true
            }
        },
        handleEdit (id) {},
        handleOk () {
            const model = this.$refs.nvrFormModal.model
            this.confirmLoading = true
            this.$refs.nvrFormModal.$refs.nvrForm.validate((valid) => {
                if (!valid) {
                    return false
                }
                editNvr(model).then((response) => {
                    message.success('保存成功')
                    this.visible = false
                    this.confirmLoading = false
                    this.refresh()
                })
            })
        },
        handleCancel () {
            this.visible = false
        },
        resetSearchForm () {
            this.queryParams = {
                keywords: '',
                status: '',
                parterId: ''
            }
        },
        refresh (resetSearch = true) {
            this.loadData()
            if (resetSearch) {
                this.resetSearchForm()
            }
        },
        showSizeChange (current, perPage) {
            this.queryParams = Object.assign(this.queryParams, {
                offset: current,
                limit: perPage
            })
            this.refresh()
        },
        changePaginator (page, pageSize) {
            this.queryParams = Object.assign(this.queryParams, {
                offset: page,
                limit: pageSize
            })
            this.refresh()
        },
        async getStatusItems () {
            this.statusItems = await getStatusItems()
        },
        async loadData () {
            this.loading = true
            const { devices, paginator } = await getNvrDevices(this.queryParams)
            this.nvrDevices = devices
            this.paginator = paginator
            this.loading = false
        },
        async getThirdPartyItems () {
            this.leftLoading = true
            this.thirdPartyItems = await getThirdPartyItems(this.leftQueryParams)
            this.leftLoading = false
        },
        onSearchAll () {
            this.queryParams.parterId = ''
            this.loadData()
        },
        onTpSearch (parterId) {
            this.queryParams.parterId = parterId
            this.loadData()
        },
        onChange (value) {
            this.leftQueryParams.keywords = value
            this.getThirdPartyItems()
        },
        onCellChange (key, dataIndex, value) {
            if (!value) {
                return false
            }

            const formData = { device_name: value, scenario: 'changeDeviceName' }
            editNvr(key, formData)
                .then((response) => {
                    this.refresh()
                })
                .catch((error) => {
                    this.$message.error(error.message || '修改出错了')
                })
        },
    },
    filters: {
        filterTime (time) {
            return moment(time).format('YYYY-MM-DD HH:mm')
        },
        statusTypeFilter (type) {
            const statusTypeMap = {
                0: 'warning',
                '-1': 'error',
                1: 'success'
            }
            return statusTypeMap[type]
        }
    }
}
</script>

