            overflow-y: scroll;
        }
        .device-info-right {
            padding: 20px 40px;
        }
    }

    .device-info-left {
        border-right: 1px solid #e8e8e8;
        width: 260px;
        padding: 0 10px;
        .ant-menu-item {
            margin: 0;
        }
