import { mapState } from 'vuex'

const baseMixin = {
  computed: {
    ...mapState({
      user: state => state.users.user
    }),
    userRoles () {
      return this.user ? this.user.roles : []
    }
  },
  methods: {
    isSuperAdmin () {
      return this.userRoles.indexOf('ROLE_SUPER_ADMIN') >= 0
    },
    isParterAdmin () {
      return this.userRoles.indexOf('ROLE_PARTER_ADMIN') >= 0
    }
  }
}

export {
  baseMixin
}
